import type {
    FPSMonitorOptions,
} from '../types';
import type { HttpWeblogInstance } from '@/types/index';

import { BasePlugin } from '../base';
import { addMonitor, removeMonitor } from '../../util';

const defaultOptions = {
    // 更新间隔
    interval: 10 * 1000,
    // 浮点精度 0 取整
    decimals: 2,
};
function fakeRaf(callback: (...args: any[]) => void) {
    return setTimeout(callback, 1000 / 16);
}
const fakeCaf = clearTimeout;

const raf = function (this: any, callback: (...args: any[]) => void) {
    const fn = window.requestAnimationFrame
        || window.mozRequestAnimationFrame
        // @ts-ignore
        || window.webkitRequestAnimationFrame
        || fakeRaf;
    return fn.call(this, callback);
};

const caf = function (this: any, handle: number) {
    const fn = window.cancelAnimationFrame
        || window.mozCancelAnimationFrame
        // @ts-ignore
        || window.webkitCancelAnimationFrame
        || fakeCaf;
    return fn.call(this, handle);
};

export default class FPSMonitor extends BasePlugin {
    static override key = 'fpsMonitor';
    private options = {
        ...defaultOptions,
    };
    private count = 0;
    private fpsTickId!: number;
    private loopId!: number;
    private loopBeginTime!: number;
    private loopEndTime!: number;
    /**
     * 兼容写法，如果在 Weblog 初始化设置 plugins 时，不需要设置 weblog
     * @param weblog
     */
    constructor(options?: FPSMonitorOptions)
    constructor(weblog?: HttpWeblogInstance | FPSMonitorOptions, options?: FPSMonitorOptions) {
        super();
        // 兼容处理
        let opts;
        if (options) {
            opts = options;
        } else if (weblog) {
            if ((weblog as HttpWeblogInstance)?.collect) {
                this.apply(weblog as HttpWeblogInstance);
            } else {
                opts = weblog;
            }
        }
        Object.assign(this.options, options);
        addMonitor(window, 'visibilitychange', this.visibilityChange);
        this.start();
    }
    destroy() {
        this.stop();
        removeMonitor(window, 'visibilitychange', this.visibilityChange);
    }
    private visibilityChange = () => {
        const state = document.visibilityState;
        if (state === 'visible') {
            this.start();
        } else if (state === 'hidden') {
            // hidden 之后 fps 会变成 0，属于无效数据
            this.stop();
        }
    }
    start() {
        if (this.loopId || this.fpsTickId) return;
        this.loop();
        this.fpsTick();
    }
    fpsTick() {
        this.fpsTickId = raf(() => {
            this.count++;
            this.fpsTick();
        });
    }
    loop() {
        this.loopBeginTime = new Date().valueOf();
        this.loopId = window.setTimeout(() => {
            this.loopEndTime = new Date().valueOf();
            const fps = this.calculate();
            this.weblog.collect('CUSTOM', {
                key: 'fps', 
                value: { key: 'fps', value: fps }
            });
            this.count = 0;
            this.loop();
        }, this.options.interval);
    }
    stop() {
        caf(this.fpsTickId);
        window.clearTimeout(this.loopId);
        this.loopId = 0;
        this.fpsTickId = 0;
    }
    calculate() {
        const during = this.loopEndTime - this.loopBeginTime;
        // raf执行次数
        const runCount = during / this.count;
        return +(1000 / runCount).toFixed(this.options.decimals);
    }
}