/**
 * HTTP 和 BRIDGE 混合版，兼容旧版，自动加载部分插件
 */
import type { FullHybridWeblog } from '@/types/index';
import type { FullHybridWeblogInitParams } from '@/types/configuration';

import '../util/polyfill';
import BridgeWeblogger from '@/platform/hybrid';
import { BasePlugin } from '../plugins/base';
// plugins
import CrashMonitor from '../plugins/crashMonitor';
import FpsMonitor from '../plugins/fpsMonitor';
import TimingMonitor from '../plugins/timingMonitor';
import Radar from '../plugins/radar/index';
import AutoPV from '../plugins/autoPV';
export {
    SupportTypes,
    SendConfig,
    PVOptions,
    ShowOptions,
    TaskOptions,
    CustomOptions,
    BaseOptions,
} from './types';
import { addClass, initSilence } from '@/util/namespace';
export type LoggerConfigByUser = FullHybridWeblogInitParams[0];
export type WeblogInitParams = FullHybridWeblogInitParams;

class Weblog extends BridgeWeblogger {
    constructor(logConfig: FullHybridWeblogInitParams[0], base?: FullHybridWeblogInitParams[1]) {
        super(logConfig, base);
        this.initBuildInPlugins();
    }
    initBuildInPlugins() {
        const {
            autoPV, radar, fps, timing, crash, autoPVFilter
        } = this.logConfig;
        // 这三个插件不再默认开启
        fps && this.plug(FpsMonitor);
        timing && this.plug(TimingMonitor);
        crash && this.plug(CrashMonitor);
        radar && this.plug(Radar, radar);
        if (autoPV || typeof autoPV === 'undefined') {
            this.plug(AutoPV, { autoPVFilter });
        }
    }
}
// 注册到全局对象
addClass(Weblog);
initSilence();

export {
    Weblog,
    BasePlugin,
};

export default Weblog;
